import { Component, OnInit, Input } from '@angular/core';
import { Pipe, PipeTransform } from '@angular/core';

@Component({
  selector: 'app-json-viewer',
  templateUrl: './json-viewer.component.html',
  styleUrls: ['./json-viewer.component.css']
})
export class JsonViewerComponent implements OnInit {
  @Input() data: any;
  constructor() {}

  ngOnInit() {}
  clicked(e, row) {
    e.stopPropagation();

    if (typeof row.value === 'object') {
      console.log(e, typeof row.value);
    }
    const targetEl =
      e.target.tagName === 'I'
        ? e.target.parentNode.parentNode
        : e.target.parentNode;
    const children =
      targetEl.querySelector('ul') ||
      (targetEl.nextSibling &&
        targetEl.nextSibling.querySelector &&
        targetEl.nextSibling.querySelector('ul'));
    if (children && children.classList) {
      const classList = children.classList;
      if (classList.contains('open')) {
        classList.remove('open');
      } else {
        classList.add('open');
      }
    }
  }
  isEmptyObject(obj) {
    return obj && Object.keys(obj).length === 0;
  }
}

@Pipe({ name: 'type' })
export class Type implements PipeTransform {
  transform(value: any) {
    return Array.isArray(value) ? 'array' : typeof value;
  }
}
